import React from "react";

function CreateUsers() {
    return <>
    <h1>Create User</h1>
        <form>
        <div class="form-group">
                <label for="exampleInputEmail1">Name</label>
                <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Your Name"/>
                   
  </div>
        <div class="form-group">
                <label for="exampleInputEmail1">Occupation</label>
                <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Your Occupation"/>
                    </div>
            <div class="form-group">
                <label for="exampleInputEmail1">Email address</label>
                <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email"/>
                    <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small>
  </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Phone No.</label>
                    <input type="number" class="form-control" id="exampleInputPassword1" placeholder="Enter Your Phone No."/>
  </div>
                    
                        <button type="submit" class="btn btn-primary">Submit</button>
</form>
    </>
}

export default CreateUsers;